<template>
	<!-- 选择显示的位置 -->
	<u-popup round="40rpx" :show="show" @close="close" @open="open">
		<view>
			<view class="box" :style="{
				'height': 'calc(100vh - 200rpx - ' + navBarHeight + 'px)'
			}">
				<view class="box-title">显示位置</view>
				<view style="height: 10rpx;"></view>

				<!-- 不显示 -->
				<view class="select-box"
				@click="select({
					detailIdOfLocation: '',
					addressTitle: '',
				})"
				>
					<view class="select-box-item">
						<view class="select-box-item-title">不显示</view>
						<view style="height: 10rpx;"></view>
						<view class="select-box-item-text">不显示位置</view>
					</view>
				</view>
				<scroll-view :scroll-y="true" :style="'height: calc(100vh - 200rpx - ' + navBarHeight + 'px - 150rpx)'">
					<!-- 选择显示省份/直辖市，根据ovoUserSelf.location的值 -->
					<view class="select-box-2" @click="select({
						detailIdOfLocation: 'province',
						addressTitle: ovoUserSelf.location.province,
					})">
						<view class="select-box-item">
							<view class="select-box-item-title">{{ ovoUserSelf.location.province }}</view>
							<view class="select-box-item-text">显示当前一级行政区</view>
						</view>
					</view>
					<!-- 选择显示城市/直辖市的区县，根据ovoUserSelf.location的值 -->
					<view class="select-box-2" @click="select({
						detailIdOfLocation: 'city',
						addressTitle: ovoUserSelf.location.city,
					})">
						<view class="select-box-item">
							<view class="select-box-item-title">{{ ovoUserSelf.location.city }}</view>
							<view class="select-box-item-text">显示当前二级行政区</view>
						</view>
					</view>
					<!-- 选择显示区县，根据ovoUserSelf.location的值 -->
					<view class="select-box-2"
					@click="select({
						detailIdOfLocation: 'district',
						addressTitle: ovoUserSelf.location.district,
					})"
					>
						<view class="select-box-item">
							<view class="select-box-item-title">{{ ovoUserSelf.location.district }}</view>
							<view class="select-box-item-text">显示当前三行政区</view>
						</view>
					</view>
					<!-- 显示更详细位置 -->
					<view class="select-box-2" v-for="(item, index) in addressList"
					@click="select({
						detailIdOfLocation: item.id,
						addressTitle: item.title,
					})"
					>
						<view class="select-box-item">
							<view class="select-box-item-title">{{ item.title }}</view>
							<view class="select-box-item-text">{{ item.distanceText }} | {{ item.address }}</view>
						</view>
					</view>

				</scroll-view>


			</view>
		</view>
	</u-popup>
</template>

<script>
export default {
	data() {
		return {
			show: false,
			navHeight: 44,
			statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
			navBarHeight: 48,
			ovoUserSelf: {
			},
			addressList: [
			],
			result: {
				detailIdOfLocation: "",
				addressTitle: "",
			},
		}
	},
	created() {
		this.ovoUserSelf = this.$ovo.getSelf();
		uni.$on('ovoUserSelf', (res) => {
			if (this.ovoUserSelf == null || this.ovoUserSelf.location == null) {
				this.ovoUserSelf = res;
				return false;
			}
			if (res.location.latitude != this.ovoUserSelf.location.latitude && res.location.longitude != this.ovoUserSelf.location.longitude) {
				this.ovoUserSelf = res;
			}
			this.ovoUserSelf = res;
			
		});
	},
	mounted() {
		this.navHeight = 44;
		// #ifdef MP-WEIXIN
		this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
		// #endif
		this.navBarHeight = this.navHeight - (-this.statusBarHeight);
	},
	methods: {
		select(res) {
			this.result = res;
			this.$emit('select', res);
			this.close();
		},
		open() {

			this.show = true;
			this.getLocationInfoByRangeSelf();
			console.log("--open")
		},
		close() {
			this.show = false;
		},
		getLocationInfoByRangeSelf() {
			this.$kt.request.request({
				api: this.$api.business.ovo.userBind.getLocationInfoByRangeSelf,
				stateSuccess: (res) => {
					var arr = res.data;
					this.addressList = [];
					console.log(this.ovoUserSelf.location.latitude)

					for (var i = 0; i < arr.length; i++) {
						var distance = this.getDistance(
							this.ovoUserSelf.location.latitude,
							this.ovoUserSelf.location.longitude,
							arr[i].location.lat,
							arr[i].location.lng);
						var distanceText = this.getDistanceText(distance);

						this.addressList.push({
							...arr[i],
							distance,
							distanceText
						});
					}
					// 排序
					this.addressList.sort((a, b) => {
						return a.distance - b.distance;
					});

					this.$forceUpdate();

					// this.addressList = res.data;
				}
			})
		},
		// 根据经纬度计算距离，单位为米
		getDistance(lat1, lng1, lat2, lng2) {
			const lat1Rad = lat1 * Math.PI / 180;
			const lat2Rad = lat2 * Math.PI / 180;
			const lng1Rad = lng1 * Math.PI / 180;
			const lng2Rad = lng2 * Math.PI / 180;

			const a = lat1Rad - lat2Rad;
			const b = lng1Rad - lng2Rad;

			const s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
				Math.cos(lat1Rad) * Math.cos(lat2Rad) * Math.pow(Math.sin(b / 2), 2)));

			let result = s * 6378137;
			result = Math.round(result * 10000) / 10000;
			return result;
		},
		// 距离显示的文本
		getDistanceText(distance) {
			if (distance < 1) {
				return "1m";
			} else if (distance < 1000) {
				// 不保留小数
				distance = Math.round(distance);

				return distance + "m";
			} else {
				return (distance / 1000).toFixed(1) + "km";
			}
		},
	}
}
</script>

<style lang="scss" scoped>
.box {
	padding: 30rpx;
	background-color: #fff;
	border-radius: 30rpx;
}

.box-title {
	font-size: 30rpx;
	text-align: center;
	color: #999;
}

.select-box {
	border-radius: 30rpx;
	padding: 5rpx 20rpx 5rpx 20rpx;
	background-color: #f0f0f0;
	margin-bottom: 20rpx;

	.select-box-item {
		padding: 20rpx 0;
		display: flex;
		// title在左边，text在右边
		justify-content: space-between;

		.select-box-item-title {
			font-size: 28rpx;
			color: #999;
		}

		.select-box-item-text {
			font-size: 28rpx;
			color: #333;
		}
	}

}


.select-box-2 {
	border-bottom: 1px solid #eee;
	padding: 30rpx 0;

	.select-box-item {
		.select-box-item-title {
			font-size: 28rpx;
			color: #333;
			margin-bottom: 10rpx;
		}

		.select-box-item-text {
			font-size: 28rpx;
			color: #999;
		}
	}
}

.select-box-2:active {
	opacity: .7;
}
</style>
